import './App.css';
import { useEffect, useState } from 'react';
import 'animate.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import About from './components/About';
import Footer from './components/Footer';
import Main from './components/Main';
import WhatFengShuiCanDo from './components/WhatFengShuiCanDo';
import WhatIsFengShui from './components/WhatIsFengShui';
import OurServices from './components/OurServices';

function App() {
  const [btnStyle, setBtnStyle] = useState({ display: 'none !important' })
  useEffect(() => {
    AOS.init();
  }, [])
  window.onscroll = function () { scrollFunction() }

  function scrollFunction() {
    if (document.body.scrollTop > 500 || document.documentElement.scrollTop > 500) {
      setBtnStyle({ display: 'block !important' })
    } else {
      setBtnStyle({ display: 'none !important' })
    }
  }

  function topFunction() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }
    return (
      <div className="App">
        <Main />
        <OurServices />
        {/* <WhyFengShui/> */}
        <WhatIsFengShui />
        <WhatFengShuiCanDo />
        <About />
        <Footer />
        {/* <button style={btnStyle} id='scrollBtn' className="btn primary-btn p-3 d-flex justify-content-center align-items-center shadow-lg" onClick={topFunction}><iconify-icon icon="icon-park:up"></iconify-icon></button> */}
      </div>
    );
  }

  export default App;
